import React, { Component } from 'react';
import { get, getTourAuthKey, isNullOrEmpty } from 'utils';
import { connect } from 'react-redux';
import * as action from 'action';

const uri = 'tour/detailImage';

class TourDetailGallery extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
      //this.props.setHistory(this.props.history);
    }

    this.state = {
      photoUrl: '',
    }

    this.photoList = [];
  }
  
  componentDidMount() {
    window.setSwipeCallBack(
      (dist) => {
        this.props.toLeft(dist);
      },
      (dist) => {
        this.props.toRight(dist);
      },
      'dv-tour-detail-gallery');
    this.requestData();
  }

  requestData(params) {
    const { id, type } = this.props.match.params;
    
    var data = {
      ServiceKey: getTourAuthKey(),
      contentTypeId: type,
      contentId: id,
      MobileOS: window.os, 
      MobileApp: window.appName,
      imageYN: 'Y', 
      _type: 'json',
    }

    get(uri, data, 
      (res) => {
        console.log(res);
        var list = res.response.body.items.item;
        for (var i = 0; i < list.length; ++i) {
          list[i].originimgurl = list[i].originimgurl.replace('http://', '//');
          list[i].smallimageurl = list[i].originimgurl.replace('http://', '//');
          list[i].idx = i;
        }
        console.log(list);
        this.setState({
          ...this.state,
          data: list,
          photoUrl : list[0].originimgurl,
        });
      },
      (res) => {
        console.log(res);
      });
  }

  renderPhoto(idx) {
    var url = this.state.data[idx].originimgurl;
    console.log(url);
    this.setState({
      ...this.state,
      photoUrl: url,
    });
  }

  renderItems() {
    var result = [];
    if (this.state.data) {
      var list = this.state.data;
      
      list.forEach(element => {
        var url = element.smallimageurl;
        var idx = element.idx;
        result.push(
          <div className='item' onClick={ () => { this.renderPhoto(idx); }} >
            <img key={idx} src={url} />
          </div>);
      });
    }
    return (<div id='dv-tour-detail-gallery-list'>
        <div className='list'>
          {result}
        </div>
      </div>) ;
  }

  render(){
    return (
      <div className='inner-box' id='dv-tour-detail-gallery'>
        <div className='inner-body'>
          {this.renderItems()}
          <img src={this.state.photoUrl} />
        </div>
      </div>);
  }
}


const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(TourDetailGallery);